import React, { useState } from "react";
import { Mail, Sparkles, ArrowLeft, CheckCircle2 } from "lucide-react";
import "../../styles/auth.css";
import { useLanguage } from "../../i18n/LanguageContext";
import { UniversalLink as Link } from "../../database/UniversalLink";

export function ResetPassword() {
  const { t } = useLanguage();
  const [email, setEmail] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [error, setError] = useState("");

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    setIsLoading(true);

    try {
      await new Promise((resolve) => setTimeout(resolve, 1500));
      setIsSubmitted(true);
    } catch (err) {
      setError("Something went wrong. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="auth-container">
      <div className="auth-bg">
        <div className="auth-gradient-orb orb-1"></div>
        <div className="auth-gradient-orb orb-2"></div>
        <div className="auth-gradient-orb orb-3"></div>
      </div>

      <Link to="/login" className="auth-back-link">
        <ArrowLeft size={18} />
        {t("auth.forgotPassword.footer").split("?")[1]?.trim() || t("common.login")}
      </Link>

      <div className="auth-card">
        {!isSubmitted ? (
          <>
            <div className="auth-header">
              <div className="auth-icon-badge">
                <Mail className="auth-sparkle-icon" size={32} />
              </div>
              <h1 className="auth-title">{t("auth.forgotPassword.title")}</h1>
              <p className="auth-subtitle">{t("auth.forgotPassword.subtitle")}</p>
            </div>

            <form onSubmit={handleSubmit} className="auth-form">
              {error && <div className="auth-error">{error}</div>}

              <div className="auth-field">
                <label htmlFor="email" className="auth-label">
                  <Mail size={14} />
                  {t("auth.emailLabel")}
                </label>
                <div className="auth-input-wrapper">
                  <input
                    id="email"
                    type="email"
                    className="auth-input"
                    placeholder="you@example.com"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                    disabled={isLoading}
                  />
                </div>
              </div>

              <button type="submit" className="auth-submit-btn" disabled={isLoading}>
                {isLoading ? (
                  <>
                    <span className="auth-spinner"></span>
                    {t("auth.forgotPassword.submitting")}
                  </>
                ) : (
                  <>
                    <Sparkles size={18} />
                    {t("auth.forgotPassword.submit")}
                  </>
                )}
              </button>
            </form>
          </>
        ) : (
          <div className="auth-success-state">
            <div
              className="auth-icon-badge"
              style={{
                background: "rgba(52, 211, 153, 0.1)",
                borderColor: "rgba(52, 211, 153, 0.2)",
                color: "#34d399",
              }}
            >
              <CheckCircle2 size={32} />
            </div>
            <h2 className="auth-title">{t("auth.forgotPassword.successTitle")}</h2>
            <p className="auth-subtitle" style={{ marginBottom: "32px" }}>
              {t("auth.forgotPassword.successSubtitle").replace("{email}", email)}
            </p>
            <button
              className="auth-submit-btn"
              onClick={() => setIsSubmitted(false)}
              style={{
                background: "rgba(255, 255, 255, 0.05)",
                border: "1px solid rgba(255, 255, 255, 0.1)",
              }}
            >
              {t("auth.forgotPassword.tryAnother")}
            </button>
          </div>
        )}

        <div className="auth-footer">
          {t("auth.forgotPassword.footer").split("?")[0]}?{" "}
          <Link to="/login" className="auth-link">
            {t("auth.forgotPassword.footer").split("?")[1]?.trim() || t("common.login")}
          </Link>
        </div>
      </div>
    </div>
  );
}
